<p>Perfil Clase Ejecutiva: los servicios son prestados a través de un plan con pago anticipado. Este es un plan mensual. Este perfil podrá publicar algunos aspectos de su perfil, tales como: nombre, cuenta de correo electrónico, teléfono, y sólo seleccionar una comuna para su atención.</p>
